import permision from "../../../common/permission.js"
export default {
	data() {
		return {
			title: 'scanCode',
			result: ''
		}
	},
	methods: {
		async scan() {






			uni.scanCode({
				success: (res) => {
					var role = 2
					this.result = res.result
					if (role === 1) {
						uni.navigateTo({
							url: '../../sub_page/sjsb/sjsb',								
						})
					} else if (role === 2) {
						uni.navigateTo({
							url: '../../sub_page/sjgl/sjgl'
						})
					} else {
						uni.navigateTo({
							url: '../../sub_page/sjsb/sjsb'
						})
					}						
				},
				fail: (err) => {

					uni.getSetting({
						success: (res) => {
							let authStatus = res.authSetting['scope.camera'];
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: 'Hello uni-app需要使用您的相机，请在设置界面打开相关权限',
									success: (res) => {
										if (res.confirm) {
											uni.openSetting()
										}
									}
								})
							}
						}
					})

				}
			});
		}






















	}
}
